.main_Blade {
  width: 100%;
  /* height: 90vh; */
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  padding-top: 90px;
}

.w2 {
  width: 48%;
  margin: 0 20px;
  padding: 0 5px;
}

.float_text {
  width: 70%;
  margin: 0 auto;
  margin-top: 8%;
}

.next-to-video {
  position: absolute;
  padding-right: 0;
  top: 5%;
  right: 5%;
}

.content_text {
  width: 70%;
  margin: 0 auto;
  font-family: "vanio";
  font-size: 24px;
}

.top_frame_button {
  width: 70%;
  margin: 0 auto;
}

.top_frame_button button {
  border-radius: 0px !important;
}

.right-element .image {
  text-align: right;
  /* padding-right: 30%;
  padding-top: 40px; */
  position: relative;
}

.content_text u {
  text-decoration: none;
  color: red;
}

.join_waitlist {
  font-family: "vaniobold";
  font-size: 40px;
  font-weight: bolder;
}

.blank-row {
  height: 90px;
}

.blade_text {
  font-size: 50px;
  font-weight: bolder;
}

.arrow_spring {
  width: 70%;
  margin: 0 auto;
}

.line_spring {
  width: 30%;
  margin: 0 auto;
  vertical-align: top;
}

.blade_svg {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.banner3 {
  width: 20%;
  /* height: 300px; */
}

.banner2 {
  width: 40%;
  /* height: 300px; */
}

.main_br {
  display: none;
}

.main_mobile {
  display: none;
}

@media (max-width: 1366px) {
  .banner3 {
    width: 15%;
    /* height: 225px; */
  }

  .banner2 {
    width: 28%;
    /* height: 225px; */
  }
}

@media screen and (max-width: 768px) {
  .main_Blade {
    display: none;
  }

  .main_mobile {
    width: 95%;
    margin: 0 auto;
    display: block;
    border-radius: 20px;
    overflow: hidden;
    position: relative;
    padding-top: 30px;
  }

  .float_text {
    width: 85%;
  }

  .blade_text {
    font-size: 30px;
    line-height: 1.2;
    margin-top: 20%;
    text-align: center;
  }

  .react-player {
    margin: 0 auto;
  }

  .join_now_message {
    width: 95%;
    font-size: 20px;
    text-align: center;
    margin: 0 auto;
  }

  .content_text {
    width: 95%;
    font-size: 18px;
    text-align: center;
    margin: 0 auto;
  }

  .main_br {
    display: block;
  }

  .arrow_spring {
    width: 70%;
    margin-top: -20%;
  }

  .arrow_spring img {
    width: 100%;
  }

  .banner3 {
    width: 50%;
    height: 50%;
    display: none;
  }

  .banner2 {
    width: 100%;
    height: 100%;
  }
}
