.head {
  width: 100%;
  height: 7vh;
  position: fixed;
  background-color: #fafdf0;
  z-index: 10 !important;
  padding: 2% 0%;
  box-shadow: 0 4px 15px rgb(192 192 192 / 30%);
}

.navbar_width {
  width: 70%;
  height: 100%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.img_logo {
  max-width: 100%;
  height: auto;
}

@media screen and (max-width: 1440px) {
  .head {
    padding: 3% 0%;
    height: 10vh;
  }
}

@media screen and (max-width: 768px) {
  .navbar_width {
    width: 95%;
  }

  .img_logo {
    width: 40%;
    height: auto;
  }
}
